import React from 'react';
import StatBox from './stat_box';
import HighchartsReact from 'highcharts-react-official';

export default class StatViewer extends React.Component{
    constructor(props){
        super(props);
        this.state ={
            config:this.getUpdatedProps()
        }
    }

    getUpdatedProps(){
        let allowedProps = ['SETTLE_ADDED','SETTLED','PENDING_SETTLEMENT_FROM_YESTERDAY_TILL_START','REFUND_ADDED','REFUNDED','REFUND_INITIATED','REFUND_INITIATED_FROM_LAST_3_TO_START','REFUND_PENDING'],
        finalArr;

        finalArr = this.props.config.filter((currObj)=>{
            if(allowedProps.indexOf(currObj.KEY) >= 0){
                return true;
            }
            return false;
        });

        return finalArr;
    }

    getStatView(){
        return (<>
                {this.state.config.map(function(currObj){
                    return <StatBox key={currObj.KEY} {...currObj} />;
                })}
            </>);
    }
    
    renderChart(){
        const chartData = this.state.config.map(function(currObj){
            return {
                name: currObj.NAME,
                y: currObj.VALUE
            };
        })
        const options1 = {
            title:'',
            credits: false,
            series: [{
                name:'Transactions',
                type: 'pie',
                data: chartData
            }]
        };

        return <HighchartsReact options={options1} />;
    }

    render(){
        return (
            <>
            <div className="stats-viewer-container">
                <div className="pgtitle">
                        <h2>
                            {this.props.header}
                            <span className="view-details-link"><a href={this.props.dataLink}>View Details</a></span>
                        </h2>
                </div>
                <div className="row">
                    <div className="col-md-8">
                    <div className="chart-holder">
                        {this.renderChart()}
                    </div>
                    </div>
                    <div className="stat-details col-md-4">
                    <div className="row">
                        {this.getStatView()}
                    </div>
                    </div>
                </div>
            </div>
            </>
        )
    }
}