import React from 'react';
import Axios from 'axios';
import StatViewer from './stat_viewer';

export default class StatsManager extends React.Component{
    constructor(props){
        super(props);
        this.state ={
            statsConfig: {}
        };
    }
    componentWillMount() {
        this.getDashConfig();
    }

    getDashConfig(){
        Axios({
            method:'get',
            url:'https://staging-ba.airpay.co.in/newsettlement/irctc_get_details_from_api.php?dashboard_details=1'
        })
        .then((response) => {
            //handle success
            var statsConfig = response.data.DATA;
            this.setState({
                statsConfig
            });
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
    }
    getStatData(){
        let statsData = this.state.statsConfig;
        return (<>
                {Object.keys(statsData).map(function(key){
                    let configData = statsData[key],
                        dataLink,linkTitle;

                        if(key === 'SETTLEMENT'){
                            dataLink ='https://staging-ba.airpay.co.in/newsettlement/irctc_pending_txns_listing.php';
                            linkTitle = 'View Details';
                        } else {
                            dataLink ='https://staging-ba.airpay.co.in/newsettlement/irctc_refund_listing.php';
                            linkTitle = 'View Details';
                        }
                         

                    return <StatViewer key={key} config={configData} header={key} dataLink={dataLink} linkTitle={linkTitle}/>;
                })}
        </>);
    }
    
    render(){
        return (
            
            <div className="pgwrap">
                <div className="container-fluid wrap">
                    <div className="stats-manager">
                        {this.getStatData()}
                    </div>
                </div>
            </div>
        )
    }
}