import React from 'react';
import Axios from 'axios';

export default class WalletBal extends React.Component{
    constructor(props){
        super(props);
        this.state={
            showAmtNotf: false,
            startInterval: true
        }
    }

    componentWillMount() {
        this.getWalletBal();
    }

    getWalletBal(){
        
        Axios({
            method:'get',
            url:'https://staging-ba.airpay.co.in/newsettlement/irctc_get_details_from_api.php?current_balance=1'
        })
        .then((response) => {
            // handle success
            var walletBal=response.data.AMOUNT,
                thresholdVal1= response.data.THRESHOLD['1'],
                thresholdVal2= response.data.THRESHOLD['2'],
                thresholdVal3= response.data.THRESHOLD['3'],
                showAmtNotf= this.showAmtNotf(walletBal,thresholdVal1,thresholdVal2,thresholdVal3);
            
            walletBal = this.genCommaNum(walletBal.toFixed(2));

            this.setState({
                walletBal,thresholdVal1,thresholdVal2,thresholdVal3,showAmtNotf
            });
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
    }

    genCommaNum(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    showAmtNotf(walletBal,thresholdVal1,thresholdVal2,thresholdVal3){
        if(walletBal<thresholdVal1 || walletBal<thresholdVal2 || walletBal<thresholdVal3){
            return true;
        }

        return false;
    }

    render(){
        return (
            <div className="walletbal-container">
                {this.state.showAmtNotf?
                    <div className="warning-message">
                    <div className="container-fluid wrap">
                        Please add wallet balance it has gone below the threshold limit.
                    </div>
                    </div>
                :<></>}
                <div className="container-fluid walletbal-content wrap">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="walletbal-amt-container">
                                <span className="wallet-bal-amt-txt">
                                    Balance:
                                </span>
                                <span className="wallet-bal-amt">
                                    {this.state.walletBal}
                                </span>
                            </div>
                        </div>
                        <div className="col-md-6 text-right">
                            <a href="https://staging-ba.airpay.co.in/newsettlement/irctc_details.php" className="btn btn-success"><span className="fa fa-plus"></span> Add balance </a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}